body {
  background: #020202;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
  font-size: 20px;
  color:rgb(0, 0, 0)
}
.logo-image{
  width: 300px;
  height: 150px;
  border-radius: 0%;
  overflow: hidden;
  margin-top: 0px;

}
#background-video {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}
a {
  margin: 0; /* Margen */
  padding: 0; /* Margen interior */
  box-sizing: border-box; /* Regla de tamaño de caja */
  font-family: sans-serif; /* tamaño de fuente */
}

a {
  position: relative; /* Posicionamiento relativo */
  padding: 5px 5px;
  margin: 10px;
  color: #21ebff; /* color */
  text-decoration: none; /* Estilo de fuente adicional (eliminar subrayado) */
  text-transform: uppercase; /* Fuente en mayúsculas */
  letter-spacing: 2px; /* Espaciado entre caracteres */
  font-size: 20px;
  transition: 0.5s; /* Tiempo de transición */
  overflow: hidden; /* Más allá de lo oculto */

}


a::before { /* Agregar antes (borde superior izquierdo) */
  content: ''; /* Contenido */
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid #21ebff; /* Borde superior */
  border-left: 2px solid #21ebff;
  transition: 0.5s;
  transition-delay: 0.5s;
}

a::after { /* Agregar después (borde inferior derecho) */
  content: '';
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid #FF00CC;
  border-right: 2px solid #FF00CC;
  transition: 0.5s;
  transition-delay: 0.5s;
}
a:hover::before,a:hover::after {
  /* Mueve la línea del borde modificando el ancho y el alto */
  width: 100%;
  height: 100%;
  /* Tiempo de espera 0, muévase directamente para evitar que el botón cambie de color primero */
  transition-delay: 0s;
}

